import React from 'react'
import { Card, Text } from 'theme-ui'
import Section from '@components/Section'
import Logo from '@components/Logo'

const Commitment = props => (
  <Section aside title='رسالة الموقع' {...props}>
    <Card variant='paper'>
      <Text variant='p'>
        <div>
          <div>
            <h3>من أجل إعادة الإعتبار للتراث الثقافي</h3>

            <p>
              يسعى موقع بلنيار للمساهمة في إعادة الاعتبار للتراث الثقافي
              الإسلامي والعربي في موريتانيا كرسالة مبدئية مركزاً في ذلك على ما
              يرفع الهمم ويُحيي العزة لا ما يثير الهموم ويُحيي حزازات النفوس.
              كما يسعى الموقع لأن يكون بوابة حرة وغنية ومتنوعة لخدمة الباحثين
              والمهتمين بمجال التراث الثقافي بالإضافة لنشر الجديد النافع في
              مجالات الأدب والفن والفكر والسياسة والتاريخ.
            </p>
          </div>
        </div>
      </Text>
    </Card>
  </Section>
)

export default Commitment
