import React from 'react'
import { Layout, Stack, Main, Hero } from '@layout'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import ContactInfo from '@widgets/ContactInfo'
import Commitment from '@widgets/Commitment'
import ContactImage from './contact-1-1.jpg'
import KsarPattern from '@components/KsarPattern'
import MapPattern from '@components/MapPattern'
import NbeikaPattern from '@components/NbeikaPattern'
import { Button } from 'theme-ui'
import { Link } from 'gatsby'

const options = {
  settings: {
    overlayColor: 'rgb(25, 136, 124)'
  },

  buttons: {
    backgroundColor: '#1b5245',
    iconColor: 'rgba(126, 172, 139, 0.8)',
    iconPadding: '10px',
    showAutoplayButton: true,
    showCloseButton: true,
    showDownloadButton: false,
    showFullscreenButton: true,
    showNextButton: true,
    showPrevButton: true,
    showThumbnailsButton: false
  },
  caption: {
    captionAlignment: 'start',
    captionColor: '#FFFFFF',
    captionContainerPadding: '20px 0 30px 0',
    captionFontFamily: 'inherit',
    captionFontSize: 'inherit',
    captionFontStyle: 'inherit',
    captionFontWeight: 'inherit',
    captionTextTransform: 'inherit',
    showCaption: true
  },
  button: {
    position: 'absolute',
    top: '20%',
    left: '50%',
    fontFamily: 'Lato, sans-serif'
  }
}

const PageContact = props => (
  <Layout {...props}>
    <Seo title='تواصل' />
    <Hero wide>
      <Hero>
        {/* <MoudjeriaPattern /> */}
        <KsarPattern />
        <MapPattern />
        {/* <MemphisPattern /> */}
        <NbeikaPattern />

        <Divider />
        <Divider space={5} />
        <Divider space={5} />
        <Divider space={5} />
        <Divider space={5} />
        <Divider space={5} />

        <Divider />
        <Divider />
        <Divider />
        <Divider />

        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
      </Hero>
      <Divider />
      <section className='contact-one'>
        <div className='row'>
          <div className='col-lg-7'>
            <Stack>
              <Main>
                <Divider />
                <ContactInfo id='contact-one-info' />
                <Divider />
                {/* <PageTitle header='رسالة الموقع' /> */}
                <Commitment id='contact-one-risala' />
                <Divider />
              </Main>
            </Stack>
          </div>
          <div
            className='col-lg-5 d-flex wow fadeInRight'
            data-wow-duration='1500ms'
          >
            <div>
              <div className='contact-one__image'>
                <img src={ContactImage} />
              </div>
            </div>
          </div>
        </div>
      </section>
      <Divider />
    </Hero>
  </Layout>
)

export default PageContact
