import React from 'react'
import { Card, Text, IconButton } from 'theme-ui'
import { FaPhone, FaEnvelope, FaLocationArrow } from 'react-icons/fa'
import Section from '@components/Section'
import useSiteMetadata from '@helpers-blog/useSiteMetadata'

const ContactInfo = () => {
  const { phone, address, email } = useSiteMetadata()

  return (
    <Section aside title=''>
      <Card variant='paper'>
        {/* <div className='footer-widget'> */}
        {/* <h3>يسعدنا أن تتواصلوا معنا</h3> */}
        {/* <Text variant='p'>
          Suspendisse potenti. Mauris mollis diam tempus ut.
        </Text> */}
        {/* <br /> */}
        <div className='footer-widget__social'>
          <a href='https://twitter.com/Bilignar1'>
            <i className='fab fa-twitter'></i>
          </a>

          <a href='https://www.youtube.com/channel/UCkQfjGJo-lE_sBTp9al8w2g'>
            <i className='fab fa-youtube'></i>
          </a>
        </div>
        <br />
        {/* {phone && (
          <Text>
            <IconButton variant='simple' role='presentation'>
              <FaPhone />
            </IconButton>
            {phone}
          </Text>
        )} */}
        {email && (
          <Text>
            <a href='mailto:contact@bilignar.com'>
              <IconButton variant='simple' role='presentation'>
                <FaEnvelope />
              </IconButton>
            </a>
            {email}
          </Text>
        )}
        {address && (
          <Text>
            <a href='https://goo.gl/maps/3Y2K7bSCTMW3Jjnz7'>
              <IconButton variant='simple' role='presentation'>
                <FaLocationArrow />
              </IconButton>
            </a>

            {address}
          </Text>
        )}
      </Card>
    </Section>
  )
}

export default ContactInfo
